            <p style="margin: 15px">总结&分析：flutter版本的3iClass与现有原生3iClass的核心功能在各个指标对比上都基本持平，其中flutter在渲染，帧率表现中略胜原生，但在内存处理上，flutter要比原生app使用更多的内存，考虑现有原生APP对视频流进行了降帧和降分辨率等操作，结论：Flutter版与原生版3iClass性能表现一致</p>
        </div>
            </van-panel>
      </div>
    </div>

  </div>
</template>

<script>
// @ is an alias to /src
// import config from "./config";
import config from "../../../public/config";
import { ImagePreview } from 'vant';
export default {
  name: "CRM",
  components: {},
  data () {
    return {
      showNav: true, // 是否显示导航
      content: "",
      title: "",
      reason: "",
      imageSrc: "",
      active: 0,
      items: [],
      /// ///////////////////
      testDeviceShow: false,
      case1InfoShow: false,
      case2InfoShow: false,
      case1AndroidShow: false,
      case1IosShow: false,
      case2AndroidShow: false,
      case2IosShow: false,
      case1OnlineReoprtShow: false,
      case2OnlineReoprtShow: false,
      axiosShow: false,
      testRangeShow: false,
      resultShow: false,
    };
  },
  mounted () {
    this.items = config.items
  },
  methods: {
    back () {
      this.showNav = true;
    },
    clickNav (index) {
      this.active = index;
    },
    clickItem (data) {
      this.testDeviceShow = false
      this.case1InfoShow = false
      this.case2InfoShow = false
      this.case1AndroidShow = false
      this.case1IosShow = false
      this.case2AndroidShow = false
      this.case2IosShow = false
      this.case1OnlineReoprtShow = false
      this.case2OnlineReoprtShow = false
      this.axiosShow = false
      this.testRangeShow = false
      this.resultShow = false
      this.showNav = false;
      switch (this.active) {
        case 0:
          switch (data.id) {
            case 1:
              this.testDeviceShow = true
              break;
            case 2:
              this.axiosShow = true
              break;
            case 3:
              this.testRangeShow = true
              break;

            default:
              break;
          }
          break;
        case 1:
          switch (data.id) {
            case 1:
              this.case1InfoShow = true
              break;
            case 2:
              this.case1AndroidShow = true
              break;
            case 3:
              this.case1IosShow = true
              break;
            case 4:
              this.case2OnlineReoprtShow = true
              break;
            default:
              break;
          }
          break;
        case 2:
          switch (data.id) {
            case 1:
              this.case2InfoShow = true
              break;
            case 2:
              this.case2AndroidShow = true
              break;
            case 3:
              this.case2IosShow = true
              break;
            case 4:
              this.case2OnlineReoprtShow = true
              break;
            default:
              break;
          }
          break;
        case 3:
          switch (data.id) {
            case 1:
              this.resultShow = true
              break;
            default:
              break;
          }
          break;

        default:
          break;
      }
      console.log(config.items[this.active].children[data.id - 1])
      this.title = config.items[this.active].children[data.id - 1].text || "";
    },
    openImg (e) {
      console.log(e)
      ImagePreview({ images: [e.srcElement.src], closeable: true })
    },
  },
};
</script>

